<template>
  <div class="registe">
    <!-- 标题 -->
    <div class="title">
      跨企业人才管理系统
    </div>
    <div class="each_info" size="medium">
      <el-form class="apply-form first-form" :model="formData" :rules="rule" ref="form">
        <el-form-item prop="account_number" ><i class="el-icon-user"></i>用户账号

          <el-input  v-model="formData.account_number" placeholder="请输入账号" clearable></el-input>

        </el-form-item>
        <el-form-item prop="password"><i class="el-icon-lock"></i>密码

          <el-input v-model="formData.password" :type="typePwd" :maxlength="18" placeholder="请输入密码" clearable>
            <i slot="suffix" class="el-icon-view" v-bind:class="{changethecolor:changecolor}" @click="showPwd"/>
          </el-input>

        </el-form-item>
      </el-form>
      <el-row>
        <br>
        <el-button round  type="success" style="width: 250px;left: 10px;position: relative;font-size: 18px" @click="login">登录</el-button>
        <router-link to="/registe"><el-button round type="warning" style="width: 250px;left: 30px;position: relative;font-size: 18px">
          注册</el-button></router-link>

      </el-row>
      <el-row>
        <br>
        <el-button round  type="primary" style="width: 250px;font-size: 15px;left: 10px;position: relative;" @click="superLogin">管理员登录</el-button>
        <router-link to="/forgetAccount"><el-button type="danger" size="mini" style="font-size: 15px;left: 45px;position: relative;">忘记账号</el-button></router-link>
        <router-link to="/forgetPwd"> <el-button type="danger" size="mini" style="font-size: 15px;left: 70px;position: relative;">忘记密码</el-button></router-link>

      </el-row>
    </div>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import store from "../store/store";
export default {
  name: "registe",
  data() {
    return {
      changethecolor: false,
      changecolor:false,
      choice:'1',
      typePwd:'password',
      formData: {
        account_number:'',
        password: '',
        status:''
      },
      rule: {
        account_number: [
          { required: true, message: '请输入账户', trigger: 'blur' },
          { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' },
          {
            required: true,
            pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9.·-]+$/,
            message: '账户不支持特殊字符',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 18, message: '请填写6-18位密码', trigger: 'blur' },
          {
            required: true,
            pattern: /^[a-zA-Z0-9]+$/,
            message: '请输入正确的字母或数字组合',
            trigger: 'blur'
          }
        ],
      }
    }
  },
  methods: {
    showPwd() {
      this.typePwd = this.typePwd === 'password' ? 'text' : 'password';
      this.changecolor = !this.changecolor
    },

    login () {
      this.formData.status='staff'
      if (this.formData.account_number === '' || this.formData.password === '') {
        alert('账号或密码不能为空');
      } else {
        this.axios({
          method: 'post',
          url: '/log/login',
          data: this.formData
        }).then(res => {
          if (res.data.success=='success')
          {
            var millisecond = new Date().getTime();
            var expiresTime = new Date(millisecond + 60 * 1000 * 30);
            this.$cookie.set('Authorization',res.data.token, {
              expires: expiresTime,
            });//30min过期
            this.set_token(JSON.stringify(res.data.token))
            this.set_name(JSON.stringify(res.data.object.name))
            this.set_id(JSON.stringify(res.data.object.id))
            this.set_role(JSON.stringify(res.data.jurisdiction))
            this.$router.push('/homepage').catch(err => ( console.log(err)))
            alert('登陆成功');
          }else if (res.data.success=='noAccount_number'){
            alert('账号不存在')
          }else if (res.data.success=='noPass') {
            alert('账号未审核或未通过')
          }if (res.data.success=='wrong_password') {
            alert('账号或密码错误')
          }
        }).catch(error => {
          alert('出错啦，请联系管理员');
          console.log(error);
        });
      }
    },
    ...mapMutations(['set_token',"set_name","set_role","set_id"]),
    superLogin () {
      this.formData.status='super_admin'
      if (this.formData.account_number === '' || this.formData.password === '') {
        alert('账号或密码不能为空');
      } else {
        this.axios({
          method: 'post',
          url: '/log/login',
          data: this.formData
        }).then(res => {
          console.log(res)
          if (res.data.success=='success')
          {
            var millisecond = new Date().getTime();
            var expiresTime = new Date(millisecond + 60 * 1000 * 30);
            this.$cookie.set('Authorization',res.data.token, {
              expires: expiresTime,
            });
            this.set_token(JSON.stringify(res.data.token))
            this.set_name(JSON.stringify(res.data.object.name))
            this.set_id(JSON.stringify(res.data.object.id))
            this.set_role(JSON.stringify(res.data.jurisdiction))
            this.$router.push('/homepage').catch(err => ( console.log(err)))
            alert('登陆成功');
          }else if (res.data.success=='noAccount_number'){
            alert('账号不存在')
          }else {
            alert('账号或密码错误')
          }
        }).catch(error => {
          alert('出错啦，请联系管理员');
          console.log(error);
        });
      }
    }
  },
  computed: {
    showColoer: function () {
      this.changethecolor = this.changecolor
    }
  },
  mounted() {
    window.localStorage.removeItem('Authorization')
  }

}
</script>

<style>
.changethecolor{
  color: red;
}
.title {
  display: flex;
  justify-content: center;
  align-self: center;
  margin-top: 150px;
  font-size: 70px;
  /*font-family: STKaiti;*/
  color: #FFF;
  text-shadow: 5px 5px 10px black;
  z-index: 1;
}
.registe {
  width: 900px;
  height: 400px;
  margin: 60px auto 0;
  position: relative;
}
.each_info {
  justify-content: center;
  margin: 60px auto 0;
  position: relative;
  margin-left: 15px;
  width: 550px;
  height: 330px;
  left: 140px;
  background: #FFFFFF;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 10px 40px 0 rgb(0 13 97);

}
.el-input{
  outline: none;
  border: none;
  background: none;
  color: #fff;
  font-size: 18px;
}
.el-form-item__content{
  font-size: 20px;
  color: black;
}
</style>



