<template>
  <div class="registe">
    <el-backtop target=".page-component__scroll .el-scrollbar__wrap"></el-backtop>
    <!-- 标题 -->
    <div class="title">
      跨企业人才管理系统
    </div>
    <div class="each_info2" size="medium">
      <el-form class="apply-form first-form" :model="allData.formData" :rules="rules" ref="form">
        <el-form-item label="真实姓名" prop="name" style="font-size: 15px">
          <el-input class="el-input" v-model="allData.formData.name" placeholder="请输入姓名" clearable></el-input>
        </el-form-item>
        <el-form-item label="个人编号" prop="id">
          <el-input v-model="allData.formData.id"  placeholder="请输入编号" clearable></el-input>
        </el-form-item>

        <el-form-item>
          <el-alert
            v-if="showPersonId"
            title="可以使用"
            type="success"
            :closable="false"
            show-icon>
          </el-alert>
          <el-alert
            v-show="NoshowPersonId"
            title="已存在"
            type="error"
            :closable="false"
            show-icon>
          </el-alert>
        </el-form-item>

        <el-form-item label="公司编号" prop="company_id">
          <el-input v-model="allData.formData.company_id"  placeholder="请输入编号" clearable></el-input>
        </el-form-item>

        <el-form-item>
          <el-alert
            v-if="showCompanyId"
            :title="existCompany"
            type="success"
            :closable="false"
            show-icon>
          </el-alert>
          <el-alert
            v-show="NoshowCompanyId"
            title="不存在该公司"
            type="error"
            :closable="false"
            show-icon>
          </el-alert>
        </el-form-item>

        <el-form-item label="用户账号" prop="account_number">
          <el-input v-model="allData.formData.account_number" placeholder="请输入账号" clearable></el-input>
        </el-form-item>

        <el-form-item>
          <el-alert
            v-if="showAccount"
            title="可以使用"
            type="success"
            :closable="false"
            show-icon>
          </el-alert>
          <el-alert
            v-show="NoshowAccount"
            title="已存在"
            type="error"
            :closable="false"
            show-icon>
          </el-alert>
        </el-form-item>

        <el-form-item label="密码" prop="password">
          <el-input v-model="allData.formData.password" :type="typePwd1" :maxlength="18" placeholder="请输入密码(6-18位)" clearable>
            <i slot="suffix" class="el-icon-view" v-bind:class="{changethecolor1:changecolor1}" @click="showPwd1"/>
          </el-input>
        </el-form-item>
        <el-form :rules="again_passwordrule" :model="allData">
          <el-form-item label="再次输入密码" prop="again_password" >
            <el-input v-model="allData.again_password" :type="typePwd2" :maxlength="18" placeholder="请再次输入密码" clearable>
              <i slot="suffix" class="el-icon-view" v-bind:class="{changethecolor2:changecolor2}" @click="showPwd2"/>
            </el-input>
          </el-form-item>
        </el-form>
        <el-form-item label="性别:" prop="gender">
          <el-radio-group v-model="allData.formData.gender" fill="#ffff">
            <el-radio  label="男" border >男</el-radio>
            <el-radio  label="女" border>女</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="身份选择" prop="identity">
          <el-select v-model="allData.formData.identity" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="邮箱" prop="email">
          <el-input v-model="allData.formData.email"  placeholder="请输入邮箱" clearable></el-input>
        </el-form-item>
      </el-form>
      <el-row>
        <span>&nbsp&nbsp&nbsp&nbsp</span>
        <span>&nbsp&nbsp&nbsp&nbsp</span>
        <el-button round type="primary" style="width: 150px;left: 30px;position: relative;" @click="signUp">注册</el-button>
        <span>&nbsp&nbsp&nbsp&nbsp</span>
        <router-link to="/LoginChart"><el-button round type="warning" style="width: 150px;left: 30px;position: relative;">返回</el-button></router-link>
      </el-row>
    </div>
  </div>
</template>

<script>
import {signUpNewAccount,checkAccount,checkCompanyId,checkId} from "../service/registe";

export default {

  data() {
    var Pass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
        // password 是表单上绑定的字段
      } else if (value !== this.allData.formData.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      changethecolor1: false,
      changecolor1:false,
      changethecolor2: false,
      changecolor2:false,

      typePwd1:'password',
      typePwd2:'password',
      showPersonId:false,
      NoshowPersonId:false,
      showCompanyId:false,
      NoshowCompanyId:false,
      showAccount:false,
      NoshowAccount:false,

      existCompany:'',

      allData:{
        again_password: '',
        formData: {
          id:'',
          name: '',
          account_number:'',
          password: '',
          company_id:'',
          email:'',
          gender:'男',
          identity: 'hr'
        },
      },
      options: [{
        label:'hr',
        value: 'hr'
      }, {
        label:'部门主管',
        value: '部门主管'
      }],
      again_passwordrule:{
        again_password: [
          { required: true, message: '请再次输入密码', trigger: 'blur' },
          // {  max: 18, message: '请填写最多18位密码', trigger: 'blur' },
          { required: true,
            validator: Pass2,
            trigger: 'blur'
          }
        ],
      },


      rules: {
        name: [
          { required: true, message: '请输入姓名', trigger: 'blur' },
          // { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' },
          {
            required: true,
            pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9.·-]+$/,
            message: '姓名不支持特殊字符',
            trigger: 'blur'
          }
        ],
        id: [
          { required: true, message: '请输入编号', trigger: 'blur' },
          {
            required: true,
            pattern: /^[a-zA-Z0-9]+$/,
            message: '请输入正确的编号',
            trigger: 'blur'
          }
        ],
        account_number: [
          { required: true, message: '请输入账户', trigger: 'blur' },
          // { min: 2, max: 10, message: '长度在 2 到 10 个字符', trigger: 'blur' },
          {
            required: true,
            pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9.·-]+$/,
            message: '账户不支持特殊字符',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          // {  max: 18, message: '请填写最多18位密码', trigger: 'blur' },
          {
            required: true,
            pattern: /^[a-zA-Z0-9]+$/,
            message: '请输入正确的字母或数字组合',
            trigger: 'blur'
          }
        ],

        company_id: [
          { required: true, message: '请输入公司名称', trigger: 'blur' },
          {
            required: true,
            pattern: /^[\u4e00-\u9fa5_a-zA-Z0-9.·-]+$/,
            message: '请输入正确的公司名称',
            trigger: 'blur'
          }
        ],
        identity:[
          { required: true, message: '请输入身份证号', trigger: 'blur' },
          {
            required: true,
            pattern: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/,
            message: '请输入正确的身份证号格式',
            trigger: 'blur'
          }],
        gender:[
          { required: true, message: '请选择性别', trigger: 'blur' },
        ],
        email: [
          { required: true, message: '请输入邮箱', trigger: 'blur' },
          {
            required: true,
            pattern: /^([a-zA-Z0-9]+[-_.]?)+@[a-zA-Z0-9]+\.[a-z]+$/,
            message: '请输入正确的邮箱',
            trigger: 'blur'
          }]
      }
    }
  },
  methods:{
    showPwd1 (){
      this.typePwd1 = this.typePwd1 === 'password' ? 'text' : 'password';
      this.changecolor1=!this.changecolor1
    },
    showPwd2 (){
      this.typePwd2 = this.typePwd2 === 'password' ? 'text' : 'password';
      this.changecolor2=!this.changecolor2
    },
    signUp(){
      signUpNewAccount(this.allData.formData).then((res)=>{
        if (res.data.msg=='success'){
          this.allData.formData={brand_right:0}
          this.showPersonId=false,
            this.NoshowPersonId=false,
            this.showCompanyId=false,
            this.NoshowCompanyId=false,
            this.showAccount=false,
            this.NoshowAccount=false,
            this.$message( {
              message: '注册成功，等待验证',
              type: 'success'
            })
        }else {
          this.$message.error('注册失败');
          this.showPersonId=false,
            this.NoshowPersonId=false,
            this.showCompanyId=false,
            this.NoshowCompanyId=false,
            this.showAccount=false,
            this.NoshowAccount=false
        }
      }).catch((err)=>{
        this.$message.error('注册失败，请联系管理员');
        this.showPersonId=false,
          this.NoshowPersonId=false,
          this.showCompanyId=false,
          this.NoshowCompanyId=false,
          this.showAccount=false,
          this.NoshowAccount=false
      })
    }
  },
  computed:{
    showColoer1: function (){
      this.changethecolor1=this.changecolor1
    },
    showColoer2: function (){
      this.changethecolor2=this.changecolor2
    },
  },
  watch:{
    'allData.formData.id':function (val) {
      clearTimeout(this.timer);  //清除延迟执行
      this.timer = setTimeout(() => {   //设置延迟执行
        if (this.allData.formData.id == '') {
          this.showPersonId = false
          this.NoshowPersonId=false
        } else {
          checkId(JSON.stringify(val)).then((res) => {
            console.log(res.data)
            if (res.data.staff== 'miss'){
              this.showPersonId = true
              this.NoshowPersonId=false
            }else if(res.data.staff== 'exist'){
              this.showPersonId = false
              this.NoshowPersonId=true
            }
          })
        }
      }, 1000);
    },
    'allData.formData.company_id':function (val) {
      console.log(JSON.stringify(val))
      clearTimeout(this.timer);  //清除延迟执行
      this.timer = setTimeout(() => {   //设置延迟执行
        if (this.allData.formData.company_id == '') {
          this.showCompanyId = false
          this.NoshowCompanyId=false
        } else {
          checkCompanyId(JSON.stringify(val),this.allData.formData.account_number).then((res) => {
            console.log(res.data)
            if (res.data.msg.trim()== 'miss'){
              this.showCompanyId = false
              this.NoshowCompanyId=true
            }else if (res.data.msg== 'error') {

            }else {
              this.NoshowCompanyId=false
              this.showCompanyId = true
              this.existCompany=res.data.msg
            }
          })
        }
      }, 1000);
    },
    'allData.formData.account_number':function (val){
      clearTimeout(this.timer);  //清除延迟执行
      this.timer = setTimeout(()=>{   //设置延迟执行
        if (this.allData.formData.account_number == '') {
          this.showAccount = false
          this.NoshowAccount=false
        } else {
          checkAccount(JSON.stringify(val).trim()).then((res) => {
            console.log(res.data)
            if (res.data.msg== 'miss'){
              this.NoshowAccount=false
              this.showAccount = true
            }else if (res.data.msg== 'exist'){
              this.showAccount = false
              this.NoshowAccount=true
            }
          })
        }
      },1000);
    }
  }
}
</script>

<style>
.changethecolor1{
  color: red;
}
.changethecolor2{
  color: red;
}
.title {
  display: flex;
  justify-content: center;
  align-self: center;
  margin-top: 150px;
  font-size: 70px;
  /*font-family: 'SimSun';*/
  color: #FFF;
  text-shadow: 5px 5px 10px black;
  z-index: 1;
}
.registe {
  width: 900px;
  height: 400px;
  margin: 60px auto 0;
  position: relative;
}
.each_info2 {
  justify-content: center;
  position: relative;
  top:50px;
  width: 500px;
  height: 1100px;
  left: 180px;
  background: #FFFFFF;
  border-radius: 8px;
  padding: 30px;
  box-shadow: 0 10px 40px 0 rgb(0 13 97);
}
.el-input{
  outline: none;
  border: none;
  background: none;
  color: #fff;
  font-size: 18px;
}

.el-form-item__label{
  font-size: 20px;
  color: black;
}
</style>



