import Vue from 'vue'
import Router from 'vue-router'
import LoginChart from "../views/LoginChart";
import registe from "../views/registe";
import changePwd from "../views/pages/changePwd";
import store from "../store/store";

Vue.use(Router)

const router= new Router({
  // mode: 'history',
  base: '/',
  routes: [
    {
      path:'/',
        redirect:'/LoginChart',
    },
    {
      path: '/LoginChart',
      name: 'LoginChart',
      meta:{title:'登录页面'},
      component: LoginChart
    },
    {
      path: '/registe',
      name: 'registe',
      meta:{title:'注册页面'},
      component: registe
    },
    {
     path:'/forgetAccount' ,
     component:()=>import('../views/forgetAccount'),
      meta:{title: '忘记账号'}
    },
    {
      path:'/forgetPwd' ,
      component:()=>import('../views/forgetPwd'),
      meta:{title: '忘记密码'}
    },
    {
      path: '/changePwd',
      name: 'changePwd',
      meta:{title:'修改密码'},
      component: changePwd
    },
    {
      path: '/Home',
      component: () => import('../views/common/Home'),
      meta : {                      //加一个自定义obj
        requireAuth:true      //这个参数 true 代表需要登录才能进入A
      },
      children: [
        {
          path:'/homepage',
          component:()=>import('../views/pages/homepage'),
          meta:{title:'系统首页'}
        },
        {
          path:'/employeeSearch',
          component:()=>import('../views/pages/infSearch/employeeSearch'),
          meta:{title:'员工查询'}
        },
        {
          path:'/personnelSearch',
          component:()=>import('../views/pages/infSearch/personnelSearch'),
          meta:{title:'人才查询'}
        },
        {
          path:'/employeeFile',
          component:()=>import('../views/pages/informationEntry/employeeFile'),
          meta:{title:'员工档案'}
        },
        {
          path:'/employeeComments',
          component:()=>import('../views/pages/informationEntry/employeeComments'),
          meta:{title:'员工评价'}
        },
        {
          path:'/userInfCheck',
          component:()=>import('../views/pages/infCheck/userInfCheck'),
          meta:{title:'待审核信息'}
        },
        {
          path:'/allUsers',
          component:()=>import('../views/pages/infCheck/allUsers'),
          meta:{title:'所有用户'}
        },
      ]
    }
    ]
});

// 导航守卫
// 使用 router.beforeEach 注册一个全局前置守卫，判断用户是否登陆
router.beforeEach((to, from, next) => {

  if (to.path === '/LoginChart' || to.path==='/registe' || to.path==='/forgetPwd' || to.path==='/forgetAccount') {
    next();
  } else {
    let token = localStorage.getItem('Authorization')
    console.log(token)
    if (token === null || token === '') {
      next('/LoginChart');
    } else {
      next();
    }
  }
});
export default router





