import Mock from 'mockjs'
import demoList2 from "./demoList";
import userList from  "./demoList";
import checkid from "./demoList"

Mock.setup({
  timeout: 800, // 设置延迟响应，模拟向后端请求数据
});
Mock.mock('/api/test', 'post', demoList2)
Mock.mock('/api/checkId', 'post',{'data':{msg:'可以使用'}})

const Random = Mock.Random
//home页面的表格数据
Mock.mock('/getUser',{
  'data|3': [{
    'name': Random.cname(),
    'age': Random.natural(22, 40),
    'date': Random.date('yyyy-MM-dd'),
    'address': Random.county(true)
  }]
})

Mock.mock('/getLogin', 'post', req => { //路径与请求方式
  console.log(JSON.parse(req.body))
  const { username, password } = JSON.parse(req.body); //将传递进来的数据保存
  console.log(username)
  console.log(password)
  console.log(userList)
  for (let i = 0; i < userList.userdata.data.length; i++) {
    //判断userList中是否存在该用户并且用户密码是否正确
    if (username === userList.userdata.data[i].username && password === userList.userdata.data[i].password) {
      return {
        meta: {
          msg: 'success',
          status: 200
        },
        data: {
          username: userList.userdata.data[i].username,
          password: userList.userdata.data[i].password,
          token:userList.userdata.data[i].token,
        }
      }
    }
  }
  return {
    meta: {
      msg: 'error',
      status: 201
    }
  }
})
export default Mock;



