import request from '../request/api'
//注册账户
export const signUpNewAccount = (data) => {
  return request('/staff/addStaff', {
    method:'post',
    data
  })
}
//检查id合法性
export const checkId = (data) => {
  return request('/staff/findByIdStaff', {
    method:'post',
    data
  })
}
//检查公司id合法性
export const checkCompanyId = (data) => {
  return request('/company/findByIdCompany', {
    method:'post',
    data
  })
}
//检查账户合法性
export const checkAccount = (data) => {
  return request('/staff/findByAccount_number', {
    method:'post',
    data
  })
}



