import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);


const store = new Vuex.Store({
  state: {
    // 存储token
    token: '',
    role: '' ,
    worker_id:'',
    name:'',
  },

  mutations: {
    // 修改token，并将token存入localStorage
    set_token(state,token) {
      state.token = token;
      localStorage.setItem('Authorization',token)

      console.log('保存token成功！');
    },
    set_role(state,role) {
      state.role = role;
      localStorage.setItem('role',role)
      console.log('保存role成功！'+role);
    },
    set_name(state,name) {
      state.name = name;
      localStorage.setItem('name',name)
      console.log('保存name成功！'+name);
    },
    set_id(state,worker_id) {
      state.token = worker_id;
      localStorage.setItem('worker_id',worker_id)
      console.log('保存id成功！'+worker_id);
    },
    del_token() {
      this.state.token = "";
      this.state.role = "";
      localStorage.remove("Authorization");
    },
    // 可选
    setUserInfo(state, role) {
      localStorage.role = role;
    }
  },

  actions: {
    // removeToken: (context) => {
    // context.commit('set_token')
    // }
  },
})


export default store;






